/* title styles */
.my-name {
  position: relative;
  overflow: hidden;
  display: block;
  line-height: 1.2;
}

.my-name::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: #4fcca3;
  animation: a-ltr-after 2s cubic-bezier(0.77, 0, 0.18, 1) forwards;
  transform: translateX(-101%);
}

.my-name::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: var(--bg-color);
  animation: a-ltr-before 2s cubic-bezier(0.77, 0, 0.18, 1) forwards;
  transform: translateX(0);
}

.my-name::before,
.my-name::after {
  animation-delay: 0.5s;
}

.my-name::before,
.my-name::after {
  animation-delay: 0.3s;
}

@keyframes a-ltr-after {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(101%);
  }
}

@keyframes a-ltr-before {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(200%);
  }
}

.my-desc-a {
  position: relative;
  overflow: hidden;
  display: block;
  line-height: 1.2;
}

.my-desc-a::after {
  content: '';
