/* Android */

/* GENERAL STYLE */

.andy,
.head,
.antennas,
.left-antenna,
.right-antenna,
.eyes,
.left-eye,
.right-eye,
.bodys,
.arms,
.left-arm,
.right-arm,
.legs,
.left-leg,
.right-leg {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 0;
}

.android-logo {
  width: 30%;
  height: 14.6em;
  margin: auto;
  margin-top: 1.4em;
}

.andy {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
}

.andy,
.antennas,
.eyes,
.arms,
.legs {
  list-style-type: none;
}

.head,
.left-antenna,
.right-antenna,
.bodys,
.left-arm,
.right-arm,
.left-leg,
.right-leg {
  background-color: #4fcca3;
}

.head,
.bodys {
  display: block;
  margin: auto;
}

.left-antenna,
.right-antenna,
.left-eye,
.right-eye,
.left-arm,
.right-arm,
.left-leg,
.right-leg {
  display: inline-block;
}

/* HEAD */

.head {
  position: relative;
  width: 9.2em;
  height: 4.2em;
  border-radius: 50% / 100% 100% 0 0;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

.antennas,
.eyes {
  position: absolute;
  width: 9.2em;
}

/* ANTENNAS */

.antennas {
  top: -1.14em;
  left: 2.46em;
}

.left-antenna,
.right-antenna {
  width: 0.3em;
  height: 1.9em;
  border-radius: 0.24em;
}

.left-antenna {
  -webkit-transform-origin: 0% 100%;
  transform-origin: 0% 100%;
  -webkit-transform: rotate(-30deg);
  transform: rotate(-30deg);
}

.right-antenna {
  margin-left: 3.48em;
  -webkit-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
  -webkit-transform: rotate(30deg);
  transform: rotate(30deg);
}

/* EYES */

.eyes {
  top: 1.78em;
  left: 2.06em;
}

.left-eye,
.right-eye {
  width: 0.76em;
  height: 0.76em;
  background-color: #fff;
  border-radius: 50%;
  /*-webkit-box-shadow: 0em 0em 0.5em 0.5em rgba(255,255,255,0.2);
  box-shadow: 0em 0em 0.5em 0.5em rgba(255,255,255,0.2);*/
}

.right-eye {
  margin-left: 3.36em;
}

/* BODY */

.bodys {
  position: relative;
  top: 0.36em;
  width: 9.2em;
  height: 7.68em;
  border-radius: 0em 0em 1.08em 1.08em;
}

.arms,
.legs {
  position: absolute;
}

/* ARMS */

.arms {
  width: 15em;
  top: -0.18em;
  left: -2.36em;
}

.left-arm,
.right-arm {
  width: 2em;
  height: 6.24em;
  border-radius: 1em;
  -webkit-transform-origin: 50% 0%;
  transform-origin: 50% 0%;
}

.left-arm {
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

.right-arm {
  margin-left: 9.65em;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

/* LEGS */

.legs {
  top: 1.8em;
  left: 1.8em;
}

.left-leg,
.right-leg {
  width: 2.06em;
  height: 3.5em;
  margin-top: 5.66em;
  border-radius: 0em 0em 1.04em 1.04em;
}

.right-leg {
  margin-left: 1.28em;
}

/* ANIMATION CLASSES */

.anim-head {
  -webkit-animation: headAnimation 30s ease-out 3s infinite alternate;
  animation: headAnimation 30s ease-out 3s infinite alternate;
}

.anim-l-ant {
  -webkit-animation: leftAntennaAnimation 10s ease-out 3s infinite alternate;
  animation: leftAntennaAnimation 10s ease-out 3s infinite alternate;
}

.anim-r-ant {
  -webkit-animation: rightAntennaAnimation 10s ease-out 3s infinite alternate;
  animation: rightAntennaAnimation 10s ease-out 3s infinite alternate;
}

.anim-eyes {
  -webkit-animation: eyesAnimation 50s ease-in-out 0s infinite alternate;
  animation: eyesAnimation 50s ease-in-out 0s infinite alternate;
}

.anim-l-eye,
.anim-r-eye {
  -webkit-animation: blinkAnimation 7s linear 0s infinite alternate;
  animation: blinkAnimation 7s linear 0s infinite alternate;
}

.anim-arms {
  -webkit-animation: armsAnimation 30s linear 7s infinite;
  animation: armsAnimation 30s linear 7s infinite;
}

.anim-l-arm {
  -webkit-animation: leftArmAnimation 30s ease 7s infinite;
  animation: leftArmAnimation 30s ease 7s infinite;
}

.anim-r-arm {
  -webkit-animation: rightArmAnimation 30s ease 7s infinite;
  animation: rightArmAnimation 30s ease 7s infinite;
}

